import React from 'react';
import {
    Box,
    Typography,
    Paper,
    List,
    ListItem,
    ListItemText,
    Container,
    Divider
} from '@mui/material';

export default function Intro() {
    return (
        <Container maxWidth="xl">
            <Box sx={{
                flexGrow: 1,
                p: { xs: 2, sm: 3, md: 4 },
                display: 'flex',
                alignItems: 'center'
            }}>
                <Paper elevation={3} sx={{ p: 4, width: '100%' }}>
                    <Typography variant="h4" gutterBottom>
                        ASP.NET Core Minimal API Eitimi
                    </Typography>

                    <Typography variant="body1" paragraph>
                        Bu ak ders, ASP.NET Core Minimal Web APIler konusunda temel beceriler kazanmanza yardmc olacak kapsaml bir rehberdir. 20 adet aklayc YouTube videosu, 13 sunum ve 1 GitHub deposu ieren bu eitim, balangtan ileri seviyeye kadar tm kullanclar hedefler. RESTful API gelitirme, CORS ynetimi, global hata yakalama, dorulama, bamllk enjeksiyonu, AutoMapper ve daha fazlasn reneceksiniz. Pratik rnekler ve gerek dnyadan uygulamalarla desteklenen bu ierik, projelerinizi profesyonel bir seviyeye tamak iin gerekli bilgi ve aralar sunar. Balamak iin tm materyallere gz atmay unutmayn!
                    </Typography>

                    <Typography variant="body1" paragraph>

                    </Typography>



                    <Divider sx={{ my: 3 }} />

                    <Typography variant="body1">
                        Bu ders, ASP.NET Core Minimal API'leri hakknda derinlemesine bilgi edinmek isteyenler iin kapsaml bir kaynaktr.
                    </Typography>
                </Paper>
            </Box>
        </Container>
    );
}
